<template>
  <div class="app-container">
    <div id="photoCut" style="isplay: flex; flex-direction: column;">
      <el-form ref="form" label-width="80px">
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="行数">
              <el-input v-model="rows"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="列数">
              <el-input v-model="columns"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-button type="primary" @click="imgUpload">上传图片</el-button>
            <el-button type="primary" @click="downZipPic">切片下载成zip</el-button>
            <input type="file" id="back" ref="backfile" accept="image/*" @change="handleFile" style="display:none"/>
          </el-col>
        </el-row>
      </el-form>
      <div id="imgContainer">
        <img id="img" :src="imgSrc" style="" />
      </div>

      <div class="split_img_wrap">
        <div v-for="(items, index) in imgList" :key="index" class="split_img_wrap_item" :style="{ width: '100%', height: 500 / columns + 'px' }">
          <img v-for="(itemOne, indexOne) in items" :key="indexOne" :src="itemOne" :style="{ width: 500 / rows + 'px', height: 500 / columns + 'px' }" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import JsZip from "jszip";
import FileSaver from "file-saver";
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      imgList: [],
      imgSrc: null,
      imgWidth: null, //图片长宽
      imgHeight: null, //图片长宽
      rows: 3, //行数
      columns: 3, //列数
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  methods: {
    imgUpload(){
      this.$refs.backfile.click()
    }, 
    photoCut(file) {
      var _that = this;
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        _that.imgSrc = reader.result;
        let img = new Image();
        img.src = reader.result;
        img.onload = () => {
          //保存宽高
          _that.imgWidth = img.width;
          _that.imgHeight = img.height;
          _that.split();
        };
      };
    },
    handleFile(e) {
      var file = e.target.files[0];
      this.imgSrc = URL.createObjectURL(file);
      this.photoCut(file);
    },
    //拆分图片
    split() {
      var _that = this;
      var canvas = document.createElement("canvas");
      canvas.width = _that.imgWidth;
      canvas.height = _that.imgHeight;
      let ctx = canvas.getContext("2d");
      let img = document.getElementById("img");
      ctx.drawImage(img, 0, 0, _that.imgWidth, _that.imgHeight);
      let list = _that.getList(ctx);
      _that.imgList = list;
    },

    //得到切割后的图片列表
    getList(ctx) {
      var _that = this;
      let destWidth = ctx.canvas.width / _that.columns;
      let destHeight = ctx.canvas.height / _that.rows;
      let list = [];
      for (let i = 0; i < _that.rows; i++) {
        let listOne = [];
        for (let k = 0; k < _that.columns; k++) {
          console.log("i:" + i + ";k:" + k);
          let url = _that.getPart(ctx, k * destWidth, i * destHeight, destWidth, destHeight);
          listOne.push(url);
        }
        list.push(listOne);
      }
      return list;
    },
    //得到canvas的指定部分
    getPart(ctx, x, y, w, h) {
      var _that = this;
      let canvas = document.createElement("canvas");
      canvas.width = w;
      canvas.height = h;
      console.log("x:" + x + ";y:" + y + ";w:" + w + ";h:" + h);
      let data = ctx.getImageData(x, y, w, h);
      let context = canvas.getContext("2d");
      context.putImageData(data, 0, 0);
      return canvas.toDataURL("image/png", 1);
    },

    //通过url 转为blob格式的数据
    getImgArrayBuffer(url) {
      let _this = this;
      return new Promise((resolve, reject) => {
        //通过请求获取文件blob格式
        let xmlhttp = new XMLHttpRequest();
        xmlhttp.open("GET", url, true);
        xmlhttp.responseType = "blob";
        xmlhttp.onload = function () {
          if (this.status == 200) {
            resolve(this.response);
          } else {
            reject(this.status);
          }
        };
        xmlhttp.send();
      });
    },
    downZipPic() {
      if(this.imgList.length==0){
        this.$message.error("没有切图无法下载")
        return
      }
      let _this = this;
      let zip = new JsZip();
      let cache = {};
      let promises = [];
      var photoList = []
      let imgList = this.imgList;
      imgList.forEach((item,index)=>{
        item.forEach((cItem,cIndex)=>{
          var obj = {
            imageName:`${index+1}_${cIndex+1}.png`,
            href:cItem
          }
          photoList.push(obj)
        })
      })

      for (let item of photoList) {
        const promise = _this.getImgArrayBuffer(item.href).then((data) => {
          // 下载文件, 并存成ArrayBuffer对象(blob)
          zip.file(item.imageName, data, { binary: true }); // 逐个添加文件
          cache[item.imageName] = data;
        });
        promises.push(promise);
      }

      Promise.all(promises)
        .then(() => {
          zip.generateAsync({ type: "blob" }).then((content) => {
            _this.$message("正在压缩，请稍后！");
            // 生成二进制流
            FileSaver.saveAs(content, "打包图片"); // 利用file-saver保存文件  自定义文件名
            _this.$message("压缩完成！");
          });
        })
        .catch((res) => {
          console.log("111111111111");
        });
    },

    downZip() {
      //创建JSZip实例对象
      var zip = new JsZip();
      //1.创建hello.txt文件，文件内容为Hello World
      zip.file("hello.txt", "Hello World\n");
      //2.创建一个demo文件夹，文件里里创建一个hello.txt文件，文件内容为Hello World
      zip.folder("demo").file("hello.txt", "Hello World\n");
      // 生成zip文件并下载
      zip
        .generateAsync({
          type: "blob", // 压缩类型
          compression: "DEFLATE", // STORE：默认不压缩 DEFLATE：需要压缩
          compressionOptions: {
            level: 9, // 压缩等级1~9 1压缩速度最快，9最优压缩方式
          },
        })
        .then(function (content) {
          // 下载的文件名
          var filename = "hello.zip";
          // 创建隐藏的可下载链接
          var eleLink = document.createElement("a");
          eleLink.download = filename;
          eleLink.style.display = "none";
          // 下载内容转变成blob地址
          eleLink.href = URL.createObjectURL(content);
          // 触发点击
          document.body.appendChild(eleLink);
          eleLink.click();
          // 然后移除
          document.body.removeChild(eleLink);
        });
    },
  },
};
</script>
<style lang="scss" scoped>
#photoCut {
  #imgContainer {
    position: relative;
    margin-left: 150px;
    margin-top: 10px;
    margin-right: 150px;
    width: 500px;
    height: 500px;
    overflow: hidden;
    background: lightgray;
    display: inline-block;
    img {
      width: 500px;
      height: 500px;
    }
  }
  .split_img_wrap {
    width: 500px;
    height: 500px;
    display: inline-block;
  }
  .split_img_wrap_item {
    display: flex;
    img {
      box-sizing: border-box;
      border: 1px solid #ebebeb;
    }
  }
}
.tp {
  width: 300px;
  height: 300px;
}
/deep/.avatar-uploader {
  .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .el-upload:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
}
</style>
