export function notifyMe(data){
    // 先检查浏览器是否支持
    if (!("Notification" in window)) {
      alert("This browser does not support desktop notification");
    }
    // 检查用户是否同意接受通知
    else if (Notification.permission === "granted") {
      // If it's okay let's create a notification
      var notification = new Notification("测试通知标题",{
        body:"测试通知主体",
        icon:"http://image.dbbqb.com/202308252141/335186267c3c81146c41cbef41ea9c55/53xMx",//icon图标
        data:{
          address:"https://www.baidu.com"
        },
        // requireInteraction:true,//是否自动消失 true为不自动消失
        // tag:"hello notification"
      })
    }
    // 否则我们需要向用户获取权限
    else if (Notification.permission !== "denied") {
      Notification.requestPermission().then(function (permission) {
        // 如果用户接受权限，我们就可以发起一条消息
        if (permission === "granted") {
           const notification = new Notification("测试通知标题",{
            body:"测试通知主体",
            icon:"http://image.dbbqb.com/202308252141/335186267c3c81146c41cbef41ea9c55/53xMx",//icon图标
            data:{
              address:"https://www.baidu.com"
            },
            // requireInteraction:true,//是否自动消失 true为不自动消失
            // tag:"hello notification"
          })
        }
      });
    }
    console.log(notification);
    notification.onclose = (e)=>{
        console.log(e);
    }
    notification.onclick = (e)=>{
        console.log(e);
        if(e.target.data.address){
            window.open(e.target.data.address)
        }
    }
    // 最后，如果执行到这里，说明用户已经拒绝对相关通知进行授权
    // 出于尊重，我们不应该再打扰他们了
  }