<template>
  <div>
    <p>
      图片压缩
    </p>
    <br />
    <input type="file" name="" id="photo" @change="fileAdd" />
    <br />
    <p class="display-ib">
      <span>未压缩图片,压缩前大小{{ defaultImgSize }} M</span>
      <img :src="defaultImgSrc" alt="" />
    </p>
    <p class="display-ib">
      <span>压缩后图片,压缩后大小{{ compressImgSize }} M</span>
      <img :src="compressImgSrc" alt="" />
    </p>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      defaultImgSrc: null,
      defaultImgSize: null,
      compressImgSrc: null,
      compressImgSize: null,
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  methods: {
    imgUpload2(file) {
      let formdata = new FormData();
      formdata.append("file", file);
      console.log(file);
      axios.post("http://127.0.0.1:3001/uploadimg", formdata).then((res) => {
        console.log(res);
        if (res.data.code == 200) {
          photo.value = res.data.path;
        } else {
          alert(res.data.msg);
        }
      });
    },
    fileAdd(e) {
      console.log(e)
      let file = e.target.files[0];
      console.log(file)
      //判断是否为图片文件
      if (file.type.indexOf("image") == -1) {
        this.$message.error("请选择图片文件")
        return
      }else if(!/\.(jpg|jpeg|png|JPG|PNG)$/.test(file.name)){
        this.$message.error("仅支持jpg和png图片")
        return
      } else {
        let reader = new FileReader();
        let image = new Image();
        let _this = this;
        reader.readAsDataURL(file);
        reader.onload = function () {
          file.src = this.result;
          image.onload = function () {
            let width = image.width;
            let height = image.height;
            file.width = width;
            file.height = height;
            _this.imgL = file.src; //页面上显示所选择的图片
          };
          console.log(file);
          image.src = file.src; //页面上显示所选择的图片
          _this.defaultImgSrc = file.src;
          _this.defaultImgSize = parseFloat(file.size / 1024 /1204).toFixed(2);
          if (file.size / 1024 > 1025) {
            console.log(file.width);
            //判断图片的大小，超过1M 进行压缩
            _this.imgCompress(file, { quality: 0.7, img_scale: 0.5 });
          } else {
            // _this.imgUpload2(file)
          }
        };
      }
    },

    //图片压缩
    imgCompress(path, obj) {
      //path是指上传的图片，obj是压缩的品质，越低越模糊
      let _this = this; //这里的this 是把vue的实例对象指向改变为_this
      var img = new Image();
      img.src = path.src;
      img.onload = function () {
        var that = this; //这里的this 是把img的对象指向改变为that
        // 默认按比例压缩
        var w = that.width,
          h = that.height,
          scale = w / h;
        // w = obj.width || w;
        // h = obj.height || (w / scale);
        w = obj.img_scale ? w * obj.img_scale : w;
        h = obj.img_scale ? h * obj.img_scale : h;
        console.log(w, h, obj);
        var quality = 0.7; // 默认图片质量为0.7
        //生成canvas
        var canvas = document.createElement("canvas");
        var ctx = canvas.getContext("2d");
        // 创建属性节点
        var anw = document.createAttribute("width");
        anw.nodeValue = w;
        var anh = document.createAttribute("height");
        anh.nodeValue = h;
        canvas.setAttributeNode(anw);
        canvas.setAttributeNode(anh);
        ctx.drawImage(that, 0, 0, w, h);
        // 图像质量
        if (obj.quality && obj.quality <= 1 && obj.quality > 0) {
          quality = obj.quality;
        }
        // quality值越小，所绘制出的图像越模糊
        var base64 = canvas.toDataURL("image/jpeg", quality);
        // 回调函数返回base64的值
        var urlFile = _this.convertBase64UrlToBlob(base64); //这个地方的处理是为了把压缩的base64转化为对象，获得压缩后图片的大小size，方便对压缩后的图片再次进行判断；
        _this.compressImgSrc = base64;
        _this.compressImgSize = parseFloat(urlFile.size / 1024 /1024).toFixed(2);
        console.log(urlFile);
        // _this.imgUpload2(urlFile)
      };
    },

    convertBase64UrlToBlob(urlData) {
      //将base64码转化为file（Blob）
      //此处函数对压缩后的base64经过处理返回{size: "", type: ""}
      var arr = urlData.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },
  },
};
</script>
<style lang="scss" scoped>
.display-ib {
  display: inline-block;
}
img{
  width: 500px;
}
</style>
