<template>
  <div>
    <div class="box" :style="{ width, height }">
      <div class="item" :class="{ empty: item == -1 }" v-for="(item, index) in list" @click="onSwitch(item, index)" ref="item">
        {{ item != -1 ? item : "" }}
      </div>
    </div>
    <input type="text" :value="col" ref="input" />阶华容道
    <button @click="setCol">设置</button>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      list: [],
      answerList: [],
      col: 3,
      width: "",
      height: "",
    };
  },
  computed: {},
  watch: {},
  mounted() {
    this.reset();
  },
  methods: {
    setCol() {
      this.col = this.$refs.input.value;
      this.reset();
    },
    reset() {
      this.list = [];
      const maxCount = this.col * this.col;
      this.width = 101 * this.col + "px";
      this.height = 101 * this.col + "px";
      for (let i = 0; i < maxCount; i++) {
        this.list.push(i == maxCount - 1 ? -1 : i + 1);
      }
      this.answerList = JSON.parse(JSON.stringify(this.list)); //备份一份答案
      this.list = this.list.sort(() => 0.5 - Math.random());
      console.log(this.list)
      console.log(this.answerList)
    },
    onSwitch(item, index) {
      var _that = this;
      if (item != -1) {
        const lastItem = (index % this.col) + 1; //是否右边最后一个
        const eIdx = _that.list.findIndex((d) => d == -1); //空闲的位置
        console.log(item,lastItem)
        if (lastItem != _that.col || (lastItem == _that.col && _that.list[index + 1] != -1)) {
          //判断当前格如果不是右边最后一格或者当前当前格是最后一格并且下一格不是空格
          if (_that.list[index + 1] == -1 || _that.list[index - 1] == -1) {
            //如果左边或者右边有空位
            var listIndex = _that.list[index];
            var listEIdx = _that.list[eIdx];
            _that.list.splice(index,1,listEIdx)
            _that.list.splice(eIdx,1,listIndex)
          }
        }
        if (_that.list[index - _that.col] == -1) {
          var listIndex = _that.list[index];
            var listEIdx = _that.list[eIdx];
            _that.list.splice(index,1,listEIdx)
            _that.list.splice(eIdx,1,listIndex)
          //当前格子上面有格子
        }
        if (_that.list[index + _that.col] == -1) {
          //当前格子下面有格子
          var listIndex = _that.list[index];
            var listEIdx = _that.list[eIdx];
            _that.list.splice(index,1,listEIdx)
            _that.list.splice(eIdx,1,listIndex)
        }
        if (_that.list.toString() == _that.answerList.toString()) {
          setTimeout(() => {
            alert("恭喜，通关了");
            _that.reset();
          }, 1000);
        }
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.box {
  border: 1px solid rgb(218, 195, 147);
  display: flex;
  flex-wrap: wrap;
  background-color: black;
}

.item {
  width: 100px;
  height: 100px;
  background-color: rgb(247, 170, 3);
  border: 1px solid rgb(189, 133, 12);
  color: white;
  text-align: center;
  line-height: 100px;
  font-size: 20px;
  cursor: pointer;
  user-select: none;
  position: relative;
}

.empty {
  background-color: black;
  box-sizing: border-box;
}
</style>
