<template>
  <div>
    <canvas id="canvas" style="width: 100%; height: 100%;">
      当前浏览器不支持canvas,请更换浏览器再试
    </canvas>
  </div>
</template>
<script>
import { digit } from "../../utils/digit.js";
export default {
  name: "",
  components: {},
  mixins: [digit],
  props: {},
  data() {
    return {
      WINDOW_WIDTH: null,
      WINDOW_HEIGHT: null,
      RADIUS: null,
      MARGIN_TOP: null,
      MARGIN_LEFT: null,
      curShowTimeSeconds: 0,
      balls: [],
      colors: ["#ffcc00", "#ff00cc", "#ccff00", "#cc00ff", "#00ffcc", "#00ccff"],
      canvas: null,
      context: null,
    };
  },
  computed: {},
  watch: {},
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.WINDOW_WIDTH = document.body.clientWidth;
      this.WINDOW_HEIGHT = document.body.clientHeight;
      this.MARGIN_LEFT = Math.round(this.WINDOW_WIDTH / 10);
      this.MARGIN_TOP = Math.round(this.WINDOW_HEIGHT / 5);
      this.RADIUS = Math.round((this.WINDOW_WIDTH * 4) / 5 / 108) - 1;
      this.canvas = document.getElementById("canvas");
      this.canvas.width = this.WINDOW_WIDTH;
      this.canvas.height = this.WINDOW_HEIGHT;
      this.context = this.canvas.getContext("2d");
      this.curShowTimeSeconds = this.getCurrentShowTimeSeconds();
      var _this = this;
      console.log(this.context)
      setInterval(function () {
        _this.render(this.context);
        _this.update();
      }, 50);
    },
    update() {
      var nextShowTimeSeconds = this.getCurrentShowTimeSeconds();
      var nextHour = parseInt(nextShowTimeSeconds / 3600);
      var nextMinute = parseInt((nextShowTimeSeconds - nextHour * 3600) / 60);
      var nextSecond = nextShowTimeSeconds % 60;
      var curHour = parseInt(this.curShowTimeSeconds / 3600);
      var curMinute = parseInt((this.curShowTimeSeconds - curHour * 3600) / 60);
      var curSecond = this.curShowTimeSeconds % 60;
      if (nextSecond != curSecond) {
        if (parseInt(curHour / 10) != parseInt(nextHour / 10)) {
          this.addBalls(this.MARGIN_LEFT, this.MARGIN_TOP, parseInt(curHour / 10));
        }
        if (parseInt(curHour % 10) != parseInt(nextHour % 10)) {
          this.addBalls(this.MARGIN_LEFT + 15 * (this.RADIUS + 1), this.MARGIN_TOP, parseInt(curHour % 10));
        }
        if (parseInt(curMinute / 10) != parseInt(nextMinute / 10)) {
          this.addBalls(this.MARGIN_LEFT + 39 * (this.RADIUS + 1), this.MARGIN_TOP, parseInt(curMinute / 10));
        }
        if (parseInt(curMinute % 10) != parseInt(nextMinute % 10)) {
          this.addBalls(this.MARGIN_LEFT + 54 * (this.RADIUS + 1), this.MARGIN_TOP, parseInt(curMinute % 10));
        }
        if (parseInt(curSecond / 10) != parseInt(nextSecond / 10)) {
          this.addBalls(this.MARGIN_LEFT + 79 * (this.RADIUS + 1), this.MARGIN_TOP, parseInt(curSecond / 10));
        }
        if (parseInt(curSecond % 10) != parseInt(nextSecond % 10)) {
          this.addBalls(this.MARGIN_LEFT + 94 * (this.RADIUS + 1), this.MARGIN_TOP, parseInt(nextSecond % 10));
        }
        this.curShowTimeSeconds = nextShowTimeSeconds;
      }
      this.updateBalls();
    },
    updateBalls() {
      for (var i = 0; i < this.balls.length; i++) {
        this.balls[i].x += this.balls[i].vx;
        this.balls[i].y += this.balls[i].vy;
        this.balls[i].vy += this.balls[i].g;
        if (this.balls[i].y >= this.WINDOW_HEIGHT - this.RADIUS) {
          this.balls[i].y = this.WINDOW_HEIGHT - this.RADIUS;
          this.balls[i].vy = -this.balls[i].vy * 0.75;
        }
      }
      var count = 0;
      for (var i = 0; i < this.balls.length; i++) {
        if (this.balls[i].x + this.RADIUS > 0 && this.balls[i].x - this.RADIUS < this.WINDOW_WIDTH) {
          this.balls[count++] = this.balls[i];
        }
      }
      while (this.balls.length > this.count) {
        this.balls.pop();
      }
    },
    addBalls(x, y, num) {
      // for (var i = 0; i < this.digit[num].length; i++) {
      //   for (var j = 0; j < this.digit[num][i].length; j++) {
      //     if (this.digit[num][i][j] == 1) {
      //       var aBall = {
      //         x: x + j * 2 * (this.RADIUS + 1) + (this.RADIUS + 1),
      //         y: y + i * 2 * (this.RADIUS + 1) + (this.RADIUS + 1),
      //         g: 1.5 + Math.random(),
      //         vx: Math.pow(-1, Math.ceil(Math.random() * 1000)) * 4, //取4或者-4
      //         vy: -5,
      //         color: this.colors[Math.floor(Math.random() * this.colors.length)],
      //       };
      //       this.balls.push(aBall);
      //     }
      //   }
      // }
    },
    getCurrentShowTimeSeconds() {
      var curTime = new Date();
      var ret = curTime.getHours() * 3600 + curTime.getMinutes() * 60 + curTime.getSeconds();
      return ret;
    },
    render(ctx) {
      console.log(1123)
      if(!ctx) ctx = this.context;
      ctx.clearRect(0, 0, this.WINDOW_WIDTH, this.WINDOW_HEIGHT);
      var hour = parseInt(this.curShowTimeSeconds / 3600);
      var minute = parseInt((this.curShowTimeSeconds - hour * 3600) / 60);
      var second = this.curShowTimeSeconds % 60;
      this.renderDigit(this.MARGIN_LEFT, this.MARGIN_TOP, parseInt(hour / 10), ctx);
      this.renderDigit(this.MARGIN_LEFT + 15 * (this.RADIUS + 1), this.MARGIN_TOP, parseInt(hour % 10), ctx);
      this.renderDigit(this.MARGIN_LEFT + 30 * (this.RADIUS + 1), this.MARGIN_TOP, 10, ctx);
      this.renderDigit(this.MARGIN_LEFT + 39 * (this.RADIUS + 1), this.MARGIN_TOP, parseInt(minute / 10), ctx);
      this.renderDigit(this.MARGIN_LEFT + 54 * (this.RADIUS + 1), this.MARGIN_TOP, parseInt(minute % 10), ctx);
      this.renderDigit(this.MARGIN_LEFT + 69 * (this.RADIUS + 1), this.MARGIN_TOP, 10, ctx);
      this.renderDigit(this.MARGIN_LEFT + 79 * (this.RADIUS + 1), this.MARGIN_TOP, parseInt(second / 10), ctx);
      this.renderDigit(this.MARGIN_LEFT + 94 * (this.RADIUS + 1), this.MARGIN_TOP, parseInt(second % 10), ctx);
      for (var i = 0; i < this.balls.length; i++) {
        ctx.fillStyle = this.balls[i].color;
        ctx.beginPath();
        ctx.arc(this.balls[i].x, this.balls[i].y, this.RADIUS, 0, 2 * Math.PI, true);
        ctx.closePath();
        ctx.fill();
      }
    },
    renderDigit(x, y, num, ctx) {
      for (var i = 0; i < this.digit[num].length; i++) {
        for (var j = 0; j < this.digit[num][i].length; j++) {
          if (this.digit[num][i][j] == 1) {
            this.draw(x + j * 2 * (this.RADIUS + 1) + (this.RADIUS + 1), y + i * 2 * (this.RADIUS + 1) + (this.RADIUS + 1), this.RADIUS, ctx);
          }
        }
      }
    },
    draw(x, y, r, ctx) {
      ctx.beginPath();
      ctx.arc(x, y, r, 0, 2 * Math.PI);
      ctx.fillStyle = "#0b5";
      ctx.fill();
      ctx.closePath();
    },
  },
};
</script>
<style lang="" scoped></style>
