<template>
  <div>
    <div class="game">
      <canvas id="canvas" ref="canvas" width="560" height="560"></canvas>
      <p>{{ yiDongCount }}</p>
      <el-button type="primary" @click="NextLevel(-1)">上一关</el-button>
      <el-button type="primary" @click="NextLevel(1)">下一关</el-button>
      <el-button type="primary" @click="NextLevel(0)">重玩本关</el-button>
      <el-button type="primary" @click="showHelp()">游戏说明</el-button>
    </div>
    <el-dialog title="操作提示" :visible.sync="showhelp" width="30%" :before-close="handleClose">
      <span>{{ msg }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showhelp = false">取 消</el-button>
        <el-button type="primary" @click="showhelp = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import levels from "../../game/Sokoban/SokobanMapData";
//小人位置坐标
function Point(x, y) {
  this.x = x;
  this.y = y;
}
var perPosition = new Point(5, 5); //小人的初始标值
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      can: null,
      msg: null,
      cxt: null,
      w: 35,
      h: 35,
      curMap: null, //当前的地图
      curLevel: null, //当前等级的地图
      curMan: null, //初始化小人
      iCurlevel: 0, //关卡数
      moveTimes: 0, //移动了多少次
      //预加载所有图片
      oImgs: {
        block: require("../../game/Sokoban/images/block.png"),
        wall: require("../../game/Sokoban/images/wall.png"),
        box: require("../../game/Sokoban/images/box.png"),
        ball: require("../../game/Sokoban/images/ball.png"),
        up: require("../../game/Sokoban/images/up.png"),
        down: require("../../game/Sokoban/images/down.png"),
        left: require("../../game/Sokoban/images/left.png"),
        right: require("../../game/Sokoban/images/right.png"),
      },
      block: null,
      wall: null,
      box: null,
      ball: null,
      up: null,
      down: null,
      left: null,
      right: null,
      showhelp: false,
      BumpIntoAwall: 0, //撞墙次数
      yiDongCount: "",
      BumpIntoAwallError: ["这是墙!别撞!", "都说了这是墙!别撞", "这是墙!墙!墙!别再撞了"],
    };
  },
  computed: {},
  watch: {},
  mounted() {
    var _that = this;
    this.can = this.$refs.canvas;
    this.cxt = this.can.getContext("2d");
    this.keyDown();
    _that.imgPreload(_that.oImgs).then((images) => {
      _that.block = images.block;
      _that.wall = images.wall;
      _that.box = images.box;
      _that.ball = images.ball;
      _that.up = images.up;
      _that.down = images.down;
      _that.left = images.left;
      _that.right = images.right;
      _that.init();
    });
  },
  methods: {
    imgPreload(srcs) {
      return new Promise((resolve, reject) => {
        var count = 0;
        var imgNum = 0;
        var images = {};
        for (var key in srcs) {
          imgNum++;
        }
        for (var src in srcs) {
          images[src] = new Image();
          images[src].onload = function () {
            //判断是否所有的图片都预加载完成
            if (++count >= imgNum) {
              resolve(images);
            }
          };
          images[src].src = srcs[src];
        }
      });
    },
    //初始化游戏
    init() {
      //InitMap();
      //DrawMap(levels[0]);
      this.initLevel(); //初始化对应等级的游戏
      this.showMoveInfo(); //初始化对应等级的游戏数据
    },
    //绘制地板
    InitMap() {
      var _that = this;
      for (var i = 0; i < 16; i++) {
        for (var j = 0; j < 16; j++) {
          _that.cxt.drawImage(_that.block, _that.w * j, _that.h * i, _that.w, _that.h);
        }
      }
    },
    //绘制每个游戏关卡地图
    DrawMap(level) {
      var _that = this;
      for (var i = 0; i < level.length; i++) {
        for (var j = 0; j < level[i].length; j++) {
          var pic = _that.block; //初始图片
          switch (level[i][j]) {
            case 1: //绘制墙壁
              pic = _that.wall;
              break;
            case 2: //绘制陷进
              pic = _that.ball;
              break;
            case 3: //绘制箱子
              pic = _that.box;
              break;
            case 4: //绘制小人
              pic = _that.curMan; //小人有四个方向 具体显示哪个图片需要和上下左右方位值关联
              //获取小人的坐标位置
              perPosition.x = i;
              perPosition.y = j;
              break;
            case 5: //绘制箱子及陷进位置
              pic = _that.box;
              break;
          }
          //每个图片不一样宽 需要在对应地板的中心绘制地图
          _that.cxt.drawImage(pic, _that.w * j - (pic.width - _that.w) / 2, _that.h * i - (pic.height - _that.h), pic.width, pic.height);
        }
      }
    },

    //初始化游戏等级
    initLevel() {
      var _that = this;
      _that.curMap = _that.copyArray(levels[_that.iCurlevel]); //当前移动过的游戏地图
      _that.curLevel = levels[_that.iCurlevel]; //当前等级的初始地图
      _that.curMan = _that.down; //初始化小人
      _that.InitMap(); //初始化地板
      _that.DrawMap(_that.curMap); //绘制出当前等级的地图
    },
    //下一关
    NextLevel(i) {
      var _that = this;
      //iCurlevel当前的地图关数
      _that.iCurlevel = _that.iCurlevel + i;
      if (_that.iCurlevel < 0) {
        _that.iCurlevel = 0;
        return;
      }
      var len = levels.length;
      if (_that.iCurlevel > len - 1) {
        _that.iCurlevel = len - 1;
      }
      _that.initLevel(); //初始当前等级关卡
      _that.moveTimes = 0; //游戏关卡移动步数清零
      _that.showMoveInfo(); //初始化当前关卡数据
    },
    //小人移动
    go(dir) {
      var _that = this;
      var p1, p2;
      switch (dir) {
        case "up":
          _that.curMan = _that.up;
          //获取小人前面的两个坐标位置来进行判断小人是否能够移动
          p1 = new Point(perPosition.x - 1, perPosition.y);
          p2 = new Point(perPosition.x - 2, perPosition.y);
          break;
        case "down":
          _that.curMan = _that.down;
          p1 = new Point(perPosition.x + 1, perPosition.y);
          p2 = new Point(perPosition.x + 2, perPosition.y);
          break;
        case "left":
          _that.curMan = _that.left;
          p1 = new Point(perPosition.x, perPosition.y - 1);
          p2 = new Point(perPosition.x, perPosition.y - 2);
          break;
        case "right":
          _that.curMan = _that.right;
          p1 = new Point(perPosition.x, perPosition.y + 1);
          p2 = new Point(perPosition.x, perPosition.y + 2);
          break;
      }
      //若果小人能够移动的话，更新游戏数据，并重绘地图
      if (_that.Trygo(p1, p2)) {
        _that.moveTimes++;
        _that.showMoveInfo();
      }
      //重绘地板
      _that.InitMap();
      //重绘当前更新了数据的地图
      _that.DrawMap(_that.curMap);
      //若果移动完成了进入下一关
      if (_that.checkFinish()) {
        this.$confirm("恭喜通关!是否进行下一关", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "重玩本关",
          type: "warning",
        })
          .then(() => {
            _that.NextLevel(1);
          })
          .catch(() => {
            _that.NextLevel(0);
          });
      }
    },

    //判断是否推成功
    checkFinish() {
      var _that = this;
      for (var i = 0; i < _that.curMap.length; i++) {
        for (var j = 0; j < _that.curMap[i].length; j++) {
          //当前移动过的地图和初始地图进行比较，若果初始地图上的陷进参数在移动之后不是箱子的话就指代没推成功
          if ((_that.curLevel[i][j] == 2 && _that.curMap[i][j] != 3) || (_that.curLevel[i][j] == 5 && _that.curMap[i][j] != 3)) {
            return false;
          }
        }
      }
      return true;
    },
    //判断小人是否能够移动
    Trygo(p1, p2) {
      var _that = this;
      //若果超出地图的上边，不通过
      if (p1.x < 0) {
        this.$message.error("越界啦!!!!");
        return false;
      }
      //若果超出地图的左边，不通过
      if (p1.y < 0) {
        this.$message.error("越界啦!!!!");
        return false;
      }
      //若果超出地图的下边，不通过
      if (p1.x > _that.curMap.length) {
        this.$message.error("越界啦!!!!");
        return false;
      }
      //若果超出地图的右边，不通过
      if (p1.y > _that.curMap[0].length) {
        this.$message.error("越界啦!!!!");
        return false;
      }
      //若果前面是墙，不通过
      if (_that.curMap[p1.x][p1.y] == 1) {
        _that.BumpIntoAwall++;
        if (_that.BumpIntoAwall <= _that.BumpIntoAwallError.length) {
          this.$message.error(_that.BumpIntoAwallError[_that.BumpIntoAwall - 1]);
        } else {
          this.$message.error("你已经死了!");
        }
        return false;
      }
      if (_that.curMap[p1.x][p1.y] == 3 || _that.curMap[p1.x][p1.y] == 5) {
        //若果小人前面是箱子那就还需要判断箱子前面有没有障碍物(箱子/墙)
        if (_that.curMap[p2.x][p2.y] == 1 || _that.curMap[p2.x][p2.y] == 3) {
          return false;
        }
        //若果判断不成功小人前面的箱子前进一步
        _that.curMap[p2.x][p2.y] = 3; //更改地图对应坐标点的值
        //console.log(curMap[p2.x][p2.y]);
      }
      //若果都没判断成功小人前进一步
      _that.curMap[p1.x][p1.y] = 4; //更改地图对应坐标点的值
      //若果小人前进了一步，小人原来的位置如何显示
      var v = _that.curLevel[perPosition.x][perPosition.y];
      if (v != 2) {
        //若果刚开始小人位置不是陷进的话
        if (v == 5) {
          //可能是5 既有箱子又陷进
          v = 2; //若果小人本身就在陷进里面的话移开之后还是显示陷进
        } else {
          v = 0; //小人移开之后之前小人的位置改为地板
        }
      }
      //重置小人位置的地图参数
      _that.curMap[perPosition.x][perPosition.y] = v;
      //若果判断小人前进了一步，更新坐标值
      perPosition = p1;
      _that.BumpIntoAwall = 0;
      //若果小动了 返回true 指代能够移动小人
      return true;
    },

    //完善关卡数据及游戏说明
    showMoveInfo() {
      this.yiDongCount = "第" + (this.iCurlevel + 1) + "关 移动次数: " + this.moveTimes;
    },
    //游戏说明
    showHelp() {
      this.showhelp = !this.showhelp;
      if (this.showhelp) {
        this.msg = "用键盘上的上、下、左、右键移动小人，把箱子全部推到小球的位置即可过关。箱子只可向前推，不能往后拉，并且小人一次只能推动一个箱子。";
      } else {
        this.showMoveInfo();
      }
    },

    //克隆二维数组
    copyArray(arr) {
      var b = []; //每次移动更新地图数据都先清空再添加新的地图
      for (var i = 0; i < arr.length; i++) {
        b[i] = arr[i].concat(); //链接两个数组
      }
      return b;
    },
    handleClose() {
      this.showhelp = false;
    },
    keyDown() {
      // 监听键盘
      var _that = this;
      document.onkeydown = (e) => {
        //事件对象兼容
        let e1 = e || window.event || arguments.callee.caller.arguments[0]; //键盘按键判断:左箭头-37;上箭头-38；右箭头-39;下箭头-40 //左
        switch (e1.key) {
          case "ArrowLeft": //左键头
            _that.go("left");
            break;
          case "ArrowUp": //上键头
            _that.go("up");
            break;
          case "ArrowRight": //右箭头
            _that.go("right");
            break;
          case "ArrowDown": //下箭头
            _that.go("down");
            break;
        }
      };
    },
  },
};
</script>
<style lang="scss" scoped>
body {
  overflow: hidden;
}
.game {
  width: 560px;
  margin: 50px auto;
}
</style>
