<template>
  <div>
    <!-- 这是客户详情的数据,其余页面没有数据和页面,会空白 -->
    <div id="customerBaseInfo" class="customer_items">这是客户详情一的数据</div>
    <div id="customerBaseInfo1" class="customer_items">这是客户详情二的数据</div>
    <div id="customerBaseInfo2" class="customer_items">这是客户详情三的数据</div>
    <div id="customerBaseInfo3" class="customer_items">这是客户详情四的数据</div>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  mixins: [],
  props: {
    customerDetailPoint: {
      type: String,
    },
  },
  data() {
    return {};
  },
  computed: {},
  watch: {
    customerDetailPoint(n, o) {
      console.log(n, o);
      this.goAnchor('#'+n)
      document.querySelector('#'+n).scrollIntoView({
        behavior:'smooth'
      })
    },
  },
  mounted() {},
  methods: {
    //锚点跳转
    goAnchor(selector) {
      var anchor = this.$el.querySelector(selector); //获取元素
      let total;
      let disFixed = document.getElementsByClassName("vux-fixed")[0];
      if (disFixed) {
        total = anchor.offsetTop - 40;
      } else {
        total = anchor.offsetTop - 80;
      }
      let distance = document.documentElement.scrollTop || document.body.scrollTop;
      // 计算每一小段的距离
      let step = total / 6;
      (function smoothDown() {
        if (distance < total) {
          distance += step;
          // 移动一小段
          document.body.scrollTop = distance;
          document.documentElement.scrollTop = distance;
          // 设定每一次跳动的时间间隔为10ms
          setTimeout(smoothDown, 10);
        } else {
          // 限制滚动停止时的距离
          document.body.scrollTop = total;
          document.documentElement.scrollTop = total;
        }
      })();
    },
  },
};
</script>
<style lang="scss" scoped>
.customer_items {
  height: 400px;
}
</style>
