<template>
  <div class="app-container">
    <input type="file" ref="fileInput" @change="handleFile" style="display: none;" />
    <el-button type="primary" @click="targetFileUpload">图片上传</el-button>
    <img :src="sliceImgSrc" alt="" id="img" style="display:none"/>
    <img v-for="(item,index) in sliceImgList" :src="item" alt="" :key="index"/>

  </div>
</template>
<script>
import { setTimeout } from 'timers';

export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      sliceImgSrc: null,
      sliceImgList: [],
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  methods: {
    targetFileUpload() {
      let file = this.$refs.fileInput.click();
    },
    


    photoCut(file) {
      var _that = this;
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        _that.sliceImgSrc = reader.result;
        let img = new Image();
        img.src = reader.result;
        img.onload = () => {
          //保存宽高
          _that.imgWidth = img.width;
          _that.imgHeight = img.height;
          _that.split();
        };
      };
    },
    handleFile(e) {
      var file = e.target.files[0];
      this.sliceImgSrc = URL.createObjectURL(file);
      this.photoCut(file);
    },
    //拆分图片
    split() {
      var _that = this;
      var canvas = document.createElement("canvas");
      canvas.width = _that.imgWidth;
      canvas.height = _that.imgHeight;
      let ctx = canvas.getContext("2d");
      let img = document.getElementById("img");
      ctx.drawImage(img, 0, 0, _that.imgWidth, _that.imgHeight);
      let list = _that.getList(ctx);
      this.sliceImgList = list
      console.log(list)
      this.uploadChunk(list)
    },

    //得到切割后的图片列表
    getList(ctx) {
      var _that = this;
      let destWidth = ctx.canvas.width / 1;
      let destHeight = ctx.canvas.height / 3;
      let list = [];
      for (let i = 0; i < 3; i++) {
        let listOne = [];
        for (let k = 0; k < 1; k++) {
          console.log("i:" + i + ";k:" + k);
          let url = _that.getPart(ctx, k * destWidth, i * destHeight, destWidth, destHeight);
          listOne.push(url);
        }
        list.push(listOne);
      }
      return list;
    },
    //得到canvas的指定部分
    getPart(ctx, x, y, w, h) {
      var _that = this;
      let canvas = document.createElement("canvas");
      canvas.width = w;
      canvas.height = h;
      console.log("x:" + x + ";y:" + y + ";w:" + w + ";h:" + h);
      let data = ctx.getImageData(x, y, w, h);
      let context = canvas.getContext("2d");
      context.putImageData(data, 0, 0);
      return canvas.toDataURL("image/png", 0.7);
    },


    convertBase64UrlToBlob(urlData) {
      //将base64码转化为file（Blob）
      //此处函数对压缩后的base64经过处理返回{size: "", type: ""}
      var arr = urlData.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },



    uploadChunk(list,index=0) {
      var urlFile = this.convertBase64UrlToBlob(list[index][0]); //这个地方的处理是为了把压缩的base64转化为对象，获得压缩后图片的大小size，方便对压缩后的图片再次进行判断；
      console.log(urlFile)
      const formData = new FormData();
      formData.append("file", urlFile);
      this.$axios.post("http://127.0.0.1:3001/uploadimgBlob", formData).then((res) => {
        console.log(res);
        if (res.data.code == 200) {
          if(index<list.length -1){
            this.uploadChunk(list,index+1)
          }else{

          }
        } else {
        }
      });
    },
  },
};
</script>
<style lang="" scoped></style>
