<template>
  <section class="app-main">
    <navbar ref="navbar" :hitokotoText="hitokotoText"></navbar>
    <tag-bar :showTagBarList="tagBarList" :showtagBarPathList="tagBarPathList" ref="tagbar"></tag-bar>
    <transition name="fade" mode="out-in">
      <el-scrollbar :style="{height:viewHeight}" :native="false" wrapStyle="" wrapClass="" viewClass="" viewStyle="" :noresize="false" tag="section">
        <router-view></router-view>
      </el-scrollbar>
    </transition>
  </section>
</template>

<script>
import navbar from "../../../components/navbar/navbar";
import tagBar from "../../../components/tags/index";
export default {
  name: "AppMain",
  data() {
    return {
      viewHeight:'600px',
      timer:null,
      hitokotoText:null
    }
  },
  components: {
    navbar,
    tagBar,
  },
  computed: {
    tagBarList() {
      return this.$store.state.tagBarList;
    },
    tagBarPathList() {
      return this.$store.state.tagBarPathList;
    },
    // key() {
    //   return this.$route.name !== undefined ? this.$route.name + +new Date() : this.$route + +new Date()
    // }
  },
  methods: {
    getAwordOfTheDay() {
      this.$axios
        .get("https://v1.hitokoto.cn/")
        .then((res) => {
          if (res.status == 200) {
            const h = this.$createElement;
            var hitokoto = res.data.hitokoto;
            var hitokotoForm = res.data.from;
            var hitokotoFromWho = res.data.from_who;
            var showText = '';
            if(hitokotoForm&&hitokotoFromWho){
              showText = `${hitokoto} ----- ${hitokotoFromWho}[${hitokotoForm}]`
            }else if(hitokotoForm){
              showText = `${hitokoto} ----- ${hitokotoForm}`
            }else if(hitokotoFromWho){
              showText = `${hitokoto} ----- ${hitokotoFromWho}`
            }
            this.hitokotoText = showText;
          }
        })
        .catch((err) => {
        });
    },
  },
  mounted() {
    var navbar = this.$refs.navbar;
    var tagbar = this.$refs.tagbar;
    var nh = navbar.$el.clientHeight;
    var th = tagbar.$el.clientHeight;
    var viewHeight= window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
    this.viewHeight =  (viewHeight - nh - th) + 'px';
    this.getAwordOfTheDay()
    this.timer = setInterval(() => {
      this.getAwordOfTheDay()
    }, this.hitokotoShowTime);
  },
  beforeDestroy(){
    clearInterval(this.timer)
    this.timer = null;
  }
};
</script>
