<template>
  <div class="music_lyrics">
    <p class="music_name" v-for="(item,index) in musicList" :key="index">音乐名:<span @click="playMedia(item)">{{item.music_name}}</span></p>
    <!-- 歌词 -->
    <div v-show="isPlayMedia" class="song_lyrics">
      <audio :src="mediaSrc" ref="mediaRef" @timeupdate="mediaTimeUpdate"></audio>
      <ul v-if="isPlayMedia" ref="song_lyrics_ref">
        <li v-for="(item, index) in song_lyrics" :key="index">
          <!-- isThisSongLyrics(item[0],item[1]) -->
          <p
            :text="item[2]"
            :start="item[0]"
            :end="item[1]"
            :class="['song_lyrics_p', isThisSongLyrics(item[0], item[1], index) ? 'isActive' : '']"
            :song_lyrics_index="song_lyrics_index"
            :style="{ '--single_song_lyrics_time': item[3] }"
          >
            {{ item[2] }}
          </p>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      isPlayMedia: false,
      mediaSrc: null,
      song_lyrics: [],
      nowMediaTime: null,
      mediaTime: null,
      song_lyrics_index: null,
      musicList:[]
    };
  },
  computed: {
   
  },
 watch:{
  "song_lyrics_index":function(newValue, oldValue){
      let { offsetHeight } = this.$refs.song_lyrics_ref;
      if ((newValue - 1) * 31 > offsetHeight / 2) {
        this.$refs.song_lyrics_ref.scrollTop += 31;
      }
  }
 },
  mounted() {
    this.$axios.get("file/musicData.json").then((res) => {
      console.log(res)
      if(res.status == 200){
        var music = res.data.music;
        music.forEach(item=>{
          item.music_path = window.location.origin + '/file/'+item.music_path;
        })
        this.musicList = music;
      }
    }).catch((err) => {
      console.log(err)
    });
  },
  methods: {
    getMediaUrl(url){
      this.$axios.get(url,{responseType: "blob"}).then((res) => {
          const url = (window.URL || window.webkitURL).createObjectURL(new Blob([res.data]));
          this.mediaSrc = url;
          console.log(url)
      }).catch((err) => {
        console.log(err)
      });
    },
    playMedia(item) {
      let media = this.$refs.mediaRef;

      if (this.mediaSrc != item.music_path) {
        if (this.isPlayMedia && this.$refs.song_lyrics_ref) {
          this.$refs.song_lyrics_ref.scrollTop = 0;
        }
        // console.log()
        this.getMediaUrl(item.music_path)
        // this.mediaSrc = item.music_path;
        this.isPlayMedia = false;
        media.load();
      }

      setTimeout(() => {
        this.mediaTime = media.duration;
        if (item.music_lyrics) {
          this.song_lyrics = this.setSongLyrics(item.music_lyrics);
        } else {
          this.song_lyrics = [];
        }
      }, 10);
      this.isPlayMedia = true;
      if (this.isPlayMedia) {
        setTimeout(() => {
          media.play();
        }, 200);
      } else {
        setTimeout(() => {
          media.pause();
        }, 200);
      }
    },
    setSongLyrics(item) {

      var song_lyrics = item.split("\n");
      var array = [];
      song_lyrics.forEach((sitems, sindex) => {
        var itemText = sitems.split("]");
        var song_lyrics_text = itemText[1];
        var song_lyrics_time = itemText[0];
        var song_lyrics_time_start_ms = this.timeSetting(song_lyrics_time.replace("[", ""));
        var song_lyrics_time_start = this.toMiner(song_lyrics_time_start_ms);
        var nextSongLyriceText;
        var next_song_lyrics_time;
        var song_lyrics_time_end_ms;
        var song_lyrics_time_end;
        let media = this.$refs.mediaRef;
        let mediaTimes = media.duration;
        media.volume = 0.6;
        //下一行
        var nextSongLyrice = song_lyrics[sindex + 1];
        if (nextSongLyrice) {
          nextSongLyriceText = nextSongLyrice.split("]");
          next_song_lyrics_time = nextSongLyriceText[0];
          song_lyrics_time_end_ms = this.timeSetting(next_song_lyrics_time.replace("[", ""));
          song_lyrics_time_end = this.toMiner(song_lyrics_time_end_ms);
          var animationTime = song_lyrics_time_end - song_lyrics_time_start + "s";
          array.push([song_lyrics_time_start, song_lyrics_time_end, song_lyrics_text, animationTime]);
        } else {
          var animationTime = mediaTimes - song_lyrics_time_start + "s";
          array.push([song_lyrics_time_start, mediaTimes, song_lyrics_text, animationTime]);
        }
        // console.log('歌词文本===========',song_lyrics_text)
        // console.log('歌词开始时间===========',song_lyrics_time_start)
        // console.log('下一行歌词===========',nextSongLyrice)
        // console.log('下一行歌词时间song_lyrics_time_end===========',song_lyrics_time_end)
      });
      // console.log(array)
      return array;
    },
    mediaTimeUpdate(e) {
      let { currentTime } = e.target;
      this.nowMediaTime = this.secondsToMiner(currentTime.toString());
    },
    timeSetting(time) {
      //00:00:00.xxx
      let timeList = time.split(":");
      var h = 0,
        m = 0,
        s = 0;
      if (timeList.length > 2) {
        h = Number(timeList[0]);
        m = Number(timeList[1]);
        s = Number(timeList[2]);
      } else if (timeList.length > 1) {
        h = 0;
        m = Number(timeList[0]);
        s = Number(timeList[1]);
      }
      return [h, m, s];
    },
    secondsToMiner(seconds) {
      let secondsTimeMiner = seconds;
      return secondsTimeMiner;
    },
    toMiner([hour = 0, min = 0, seconds = 0]) {
      var resultMiner;
      resultMiner = hour * (60 * 60) + min * 60 + seconds;
      return resultMiner;
    },
    isThisSongLyrics(startTime, endTime, index) {
      var result = false;
      if (this.nowMediaTime >= startTime && this.nowMediaTime < endTime) {
        this.song_lyrics_index = index;
        result = true;
      }
      return result;
    },
  },
};
</script>

<style lang="scss" scoped>
.music_lyrics {
  ul,
  li {
    list-style: none;
    text-align: center;
  }
  img {
    width: 50px;
  }
  .music {
    color: green;
    cursor: pointer;
    -moz-user-select: none; /* Firefox私有属性 */
    -webkit-user-select: none; /* WebKit内核私有属性 */
    -ms-user-select: none; /* IE私有属性(IE10及以后) */
    -khtml-user-select: none; /* Khtml内核私有属性 */
    -o-user-select: none; /* Opera私有属性 */
    user-select: none; /* CSS3属性 */
  }
  .song_lyrics_p {
    position: relative;
    margin: 10px 0;
  }
  .music_name{
    cursor: pointer;
  }
  /* .song_lyrics_p.isActive::before {
    content: attr(text);
    display: inline-block;
    position: absolute;
    top: 0;
    background: linear-gradient(to right, rgb(29, 218, 38), rgb(29, 218, 38)) no-repeat 0 0;
    background-size: 0% 100%;
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    animation: gc linear var(--single_song_lyrics_time) 1;
  } */
  p.isActive {
    color: rgb(29, 218, 38);
    font-size: 20px;
  }
  .song_lyrics ul {
    height: 600px;
    overflow-y: auto;
  }
}
@keyframes gc {
  0% {
    background-size: 0% 100%;
  }
  100% {
    background-size: 100% 100%;
  }
}
</style>
