<template>
  <div class="app-container">
    <input type="file" ref="fileInput" @change="readExcel" style="display:none"/>
    <el-button type="primary" @click="targetFileUpload">excel上传</el-button>
    <el-button type="primary" @click="downmoban">模板下载</el-button>
    <el-table :data="excelData" height="650" border style="width: 100%;">
      <el-table-column prop="id" label="id" align="center"> </el-table-column>
      <el-table-column prop="city" label="姓名" align="center"> </el-table-column>
      <el-table-column prop="region" label="省份" align="center"> </el-table-column>
      <el-table-column prop="name" label="地址" align="center"> </el-table-column>
      <el-table-column prop="garden_name" label="电话" align="center"> </el-table-column>
      <el-table-column prop="age" label="年龄" align="center"> </el-table-column>
    </el-table>
  </div>
</template>
<script>
import XLSX from "xlsx";
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      excelData: [],
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  methods: {
    targetFileUpload(){
      let file = this.$refs.fileInput.click();
    },
    downmoban() {
      this.$axios
        .get("file/导入模板.xlsx", {
          //静态资源文件夹public
          responseType: "blob",
        })
        .then((response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement("a");
          let fname = "导入模板.xlsx";
          link.href = url;
          link.setAttribute("download", fname);
          document.body.appendChild(link);
          link.click();
        })
        .catch((error) => {
          console.log("error:" + JSON.stringify(error));
        });
    },
    readExcel() {
      var keyMap = {
        id: "id",
        姓名: "city",
        省份: "region",
        地址: "name",
        电话: "garden_name",
        年龄: "age",
      };

      let file = this.$refs.fileInput.files[0];
      let reader = new FileReader();
      reader.onload = (e) => {
        let data = e.target.result;
        let workbook = XLSX.read(data, { type: "binary" });
        let firstSheetName = workbook.SheetNames[0];
        let worksheet = workbook.Sheets[firstSheetName];
        // console.log(worksheet);
        // var keyList = []
        // for (const key in worksheet) {
        //   var keys = key.replace(/[^\d\.]/g,'');
        //   if(parseInt(keys)==1){
        //     keyList.push(worksheet[key].v)
        //   }
        // }
        // console.log(keyList)
        var excelData = [];
        var sheetJsonData = XLSX.utils.sheet_to_json(worksheet);
        sheetJsonData.forEach((item, index) => {
          var obj = {};
          for (const key in item) {
            // console.log(key)
            obj[keyMap[key]] = item[key];
          }
          console.log(obj);
          excelData.push(obj);
        });
        console.log(XLSX.utils.sheet_to_json(worksheet));
        this.excelData = excelData;
      };
      reader.readAsBinaryString(file);
    },
  },
};
</script>
<style lang="" scoped></style>
